  align-items: center;
  padding: 4rem 2rem;
  display: flex;
}

.sr-only {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.testimonial__grid {
  flex-direction: column;
  gap: 2rem;
  max-width: 80rem;
  display: flex;

  @media (width >= 64rem) {
    & {
      grid-template-rows: repeat(2, 1fr);
      grid-template-columns: repeat(4, 1fr);
      grid-template-areas: "item1 item1 item2 item4"
                           "item3 item5 item5 item4";
      display: grid;
    }
  }
}

.testimonial__card {
  grid-area: var(--__testimonial__card-gridArea);
  box-shadow: var(--shadow-2xl);
  background: var(--__testimonial__card-background);
  --__testimonial__lead-color: var(--__testimonial__name-color);
  --__testimonial__status-color: hsl(from var(--__testimonial__name-color) h s l / .5);
