  border-radius: 8px;
  flex-direction: column;
  gap: 1.5rem;
  padding: 2rem;
  display: flex;

  &:first-of-type {
    --__testimonial__card-gridArea: item1;
    --__testimonial__card-background: var(--color-violet);
    --__testimonial__name-color: var(--color-white);
    isolation: isolate;
    position: relative;

    &:after {
      z-index: -1;
      content: url("bg-pattern-quotation.97325254.svg");
      position: absolute;
      inset: 2rem 2rem auto auto;
